@use "global" as *;

.p-works__content {
  @include mq(md) {
   display: flex;
   align-items: center;
   background-color: #fff;
   background:linear-gradient(90deg, transparent 0%, transparent 33%, #F0F0F0 23%, #F0F0F0 100%);
   padding-top: rem(60);
   padding-bottom: rem(80);
  }
}

.p-works__images {
  position: relative;
  @include mq(md) {
   width: calc(750/1260 * 100%);
  }
}

.p-works__img {
  position: relative;
}

.p-works__img::before {
  content: "";
  padding-top: calc(231/335 * 100%);
  display: block;
}

.p-works__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.p-works__text-body {
  background-color: #F0F0F0;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  padding-top: rem(156);
  padding-bottom: rem(60);
  margin-top: rem(-116);  
  @include mq(md) {
  margin-left: initial; 
  padding: 0;
  margin-top: 0;
  width: 40.5%;
  background-color: transparent;
  }
}

.p-works__text {
  text-align: center;
  font-size: rem(14);
  margin-top: rem(40);
  line-height: calc(25/14);
}

.p-works__btn {
  margin-top: rem(30);
  text-align: center;
}
