@use "global" as *;


.p-news__inner {
  @include mq(md) {
   display: flex;
   justify-content: center;
  }
}

.p-news__items {
  margin-top: rem(32);
  @include mq(md) {
   margin-top: 0;
   margin-left: rem(50);
  }
}

.p-news__item + .p-news__item {
  margin-top: rem(32);
  @include mq(md) {
   margin-top: rem(15);
  }
}