@use "global" as *;

.p-news-content {
  @include mq(md) {
   display: flex;
   align-items: center;
  }
}

.p-news-content__meta {
  display: flex;
  align-items: center;
}

.p-news-content__date {
  color: $black;
  font-size: rem(15);
  letter-spacing: 0.1em;
  line-height: 1.3;
  font-family: $english-family;
}
.p-news-content__category {
  font-size: rem(11);
  font-weight: $bold;
  letter-spacing: 0.05em;
  border-radius: 50px;
  line-height: 1;
  background-color: #E8E8E8;
  padding: rem(4) rem(18);
  margin-left: rem(15);
}
.p-news-content__link {
  margin-top: rem(10);
  @include mq(md) {
   margin-left: rem(20);

  }
}


.p-news-content__category-text {
  font-size: rem(14);
  line-height: calc(27/14);
  letter-spacing: 0.05em;

}

